@import url(https://fonts.googleapis.com/css2?family=Kiwi+Maru&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Kiwi Maru", serif}h1,h2,h3{color:#4b4b4b}h3,h4{font-weight:400;color:#646464}.App{transition:all 0.5s ease}.library-active{margin-left:30%}.song-container{min-height:60vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.song-container img{width:20%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}@media screen and (max-width: 768px){.song-container img{width:60%}}.player{min-height:20vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.time-control{width:50%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.time-control input{width:100%;-webkit-appearance:none;background:transparent;cursor:pointer}.time-control p{padding:1rem}.play-control{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:1rem;width:30%}.play-control svg{cursor:pointer}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px}input[type="range"]::-moz-range-thumb{-webkit-appearance:none;background:transparent;border:none}.track{background:lightblue;width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;-webkit-transform:translateX(0%);transform:translateX(0%);pointer-events:none}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:60%}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background-color:white;box-shadow:2px 2px 50px #ccc;overflow-y:scroll;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all 0.5s ease;opacity:0}.library h2{padding:2rem}.library-song{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:1rem 2rem 1rem 2rem;cursor:pointer;transition:background 0.5s ease}.library-song img{width:30%}.library-song:hover{background-color:lightblue}.song-description{padding-left:1rem}.song-description h3{font-size:1rem}.song-description h4{font-size:0.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.selected{background:#76b7e2}.active-library{-webkit-transform:translateX(0%);transform:translateX(0%);opacity:1}@media screen and (max-width: 768px){.library{width:100%}}nav{min-height:10vh;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}nav button{background:transparent;border:none;cursor:pointer;border:2px solid #414141;padding:0.5rem;transition:all 0.3s ease}nav button:hover{background:#414141;color:white}.button-active{background:#414141;color:white}@media screen and (max-width: 768px){nav button{z-index:10}}

